<template>
  <div id="box">
    <div class="top">
      <div class="top_type">
        <van-icon name="passed" color="#0BEC6E" />
        <span>兑换成功</span>
      </div>
      <div class="title">您此次获取的密码是</div>
      <div class="mima">
        <span v-for="(item,i) in password" :key="i">{{item}}</span>
      </div>
      <p style="color:#fff;text-align:center;font-size:11pt;">
        请按照说明输入密码,该密码从现在
        <br />开始30分钟内有效
      </p>
    </div>
    <p style="margin: 35pt auto;color:#999;text-align:center;font-size:11pt;">
      如遇密码不可用，请重新扫码
      <br />可重新免费申请新密码 或者
      <span style="color:#15BDDB;" @click="kefu()">联系客服</span>
    </p>
    <div class="code" @click="$router.push({path:'/ruzhu'})">
      <img class="codeImg" src="@/assets/images/my/daili.jpg" />
    </div>
  </div>
</template>

<script>
import { Icon, Toast, Dialog } from "vant";
import { getFreePassword, kefu } from "@/axios/getData";
import store from '@/main'

export default {
  components: {
    vanIcon: Icon
  },
  props: {},
  data() {
    return {
      password: [], //  密码列表
      item: {} //  用来重新获取密码
    };
  },
  created() {
    // 获取 URL 地址中的 openid
    if (this.$route.query.openid) {
      localStorage.setItem('openid', this.$route.query.openid);
      // 将store初始获取的 openid 更新
      store.state.userInfo.openid = localStorage.getItem('openid');
      console.log(store.state.userInfo.openid);
    }
    this.getPassword();
  },
  methods: {
    async getPassword() {
      const res = await getFreePassword();
      this.password = (res.data.number).toString().split("");

      if (res.code !== 200) {
        Toast(res.msg);
      }
    },
    //  联系客服
    kefu() {
      var that = this;
      if (that.$store.state.kefu) {
        Dialog.confirm({
          title: that.$store.state.kefu.title,
          message: that.$store.state.kefu.number
        })
          .then(() => {
            // 不加回报异常
          })
          .catch(() => {
            // 不加回报异常
          });
        return;
      }
      kefu().then(res => {
        that.$store.state.kefu = res.data;
        Dialog.confirm({
          title: res.data.title,
          message: res.data.number
        })
          .then(() => {
            // 不加回报异常
          })
          .catch(() => {
            // 不加回报异常
          });
      });
    }
  },
  computed: {},

  mounted() {}
};
</script>

<style scoped lang="scss">
#box {
  height: 100vh;
  background: #f5f5f5;
  .top {
    position: relative;
    background: #000;
    padding-top: 20pt;
    padding-bottom: 30pt;
    .top_type {
      display: flex;
      align-items: center;
      text-align: center;
      justify-content: center;
      color: #0bec6e;
      span {
        display: inline-block;
        margin-left: 10pt;
      }
    }
    .title {
      font-family: "PingFang SC Regular";
      margin-top: 15pt;
      text-align: center;
      font-size: 16pt;
      color: #fff !important;
      font-weight: 500 !important;
    }
    .mima {
      color: #fff;
      display: flex;
      padding: 20pt 30pt;
      justify-content: space-between;
      & > span {
        display: inline-block;
        border-radius: 6pt;
        // margin-right:10pt;
        text-align: center;
        padding: 10pt;
        background: #15bddb;
        font-size: 30pt;
      }
    }
  }
  .top::after {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: block;
    content: "";
    border: 15pt solid;
    bottom: -1pt;
    border-color: transparent transparent #f5f5f5 transparent;
  }
}
/* 重新获取密码 */
.again {
  margin: 20pt auto;
  width: 50%;
  border: 1.5pt solid #15bddb;
  color: #15bddb;
  text-align: center;
  padding: 15px 0;
  border-radius: 30pt;
  font-weight: 500;
  font-size: 14pt;
}
.code {
  position: fixed;
  bottom: 20px;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  .codeImg {
    // width:450px;
    // height:450px;
    width: 80%;
    margin-bottom: 15px;
  }
}
</style>



